import React from 'react'

type Props = {
	value: any,
	onChange: (value) => any,
}

const TextInput = ({ value, onChange }: Props) => (
	<input
		className="form-control"
		type="text"
		value={value}
		onChange={e => onChange(e.target.value)}
	/>
)

export default TextInput



