// @flow

import React, { Component } from 'react'
import type { PostTypes } from '../Dashboard'

import FormRow from '../ui/FormRow'
import TextInput from '../ui/TextInput'
import Checkbox from '../ui/Checkbox'

import moment from 'moment'

type Props = {
	match: {
		params: {
			type: PostTypes,
		},
	}
}

type State = {
	post: {
		id: ?number,
		type: string,
		path: string,
		title: string,
		subtitle: string,
		content: string,
		link: ?string,
		rating: ?number,
		year: ?string,
		spoilers: boolean,
		date_completed: Date,
		time_played: string,
		creator: string,
		season: string,
		platform: string,
	},
}

class Editor extends Component<Props, State> {

	props: Props

	state: State = {
		post: {
			id: null,
			type: this.props.match.params.type || 'movie',
			path: '',
			title: '',
			subtitle: '',
			content: '',
			link: '',
			rating: 1,
			year: '',
			spoilers: false,
			date_completed: moment(),
			time_played: '',
			creator: '',
			season: '',
			platform: '',
		}
	}

	render() {
		return (
			<div>
				<div>{this.state.post.type}</div>

				<pre>
					{JSON.stringify(this.state.post, null, 2)}
				</pre>

				<form>
					<FormRow
						label="Title"
						inputKey="post-title"
						input={(
							<TextInput
								value={this.state.post.title}
								onChange={v => this.updatePost('title', v)}
							/>
						)}
					/>

					<FormRow
						label="Subtitle"
						inputKey="post-subtitle"
						input={(
							<TextInput
								value={this.state.post.subtitle}
								onChange={v => this.updatePost('subtitle', v)}
							/>
						)}
					/>

					{/* content */}

					<FormRow
						label="Path"
						inputKey="post-path"
						input={(
							<TextInput
								value={this.state.post.path}
								onChange={v => this.updatePost('path', v)}
							/>
						)}
					/>

					<FormRow
						label="Link"
						inputKey="post-link"
						input={(
							<TextInput
								value={this.state.post.link}
								onChange={v => this.updatePost('link', v)}
							/>
						)}
					/>

					{/* rating */}
					{/* year */}
					<FormRow
						label="Spoilers?"
						inputKey="post-spoilers"
						input={(
							<Checkbox
								value={this.state.post.spoilers}
								onChange={v => this.updatePost('spoilers', v)}
							/>
						)}
					/>
					{/* date_completed/watched/whatever */}

					{/* GAME*/}
					{/* time played */}
					{/* platform */}

					<strong>BOOK/MUSIC ONLY</strong>
					<FormRow
						label={this.state.post.type === 'book' ? 'Author' : 'Artist'}
						inputKey="post-author"
						input={(
							<TextInput
								value={this.state.post.creator}
								onChange={v => this.updatePost('creator', v)}
							/>
						)}
					/>

					<strong>TV/COMIC ONLY</strong>
					<FormRow
						label={this.state.post.type === 'comic' ? 'Series' : 'Season'}
						inputKey="post-season"
						input={(
							<TextInput
								value={this.state.post.season}
								onChange={v => this.updatePost('season', v)}
							/>
						)}
					/>
				</form>
			</div>
		)
	}

	updatePost = (key: string, value: any) => {
		this.setState((s: State) => ({
			post: {
				...s.post,
				[key]: value,
			}
		}))
	}
}

export default Editor



